<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="corte.meta_semana"
    :progress="corte.progress"
    :name="corte.nombre"
    :depto="corte.depto"
    :days="corte.dias_laborados"
    :m3="corte.m3_persona"
    :bonus_p="corte.bono_persona"
    :bonus_tot="corte.bono_depto"
    :asis="corte.asistencia"
    :extra="corte.$_extra_m3"
    :bonus_prod="corte.bono_productividad"
    :bonus_goals="corte.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Corte',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'corte',
            userCode: this.$route.params.user,
            city: 'tijuana'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        corte() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>